html {
  font-size: 62.5%;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 3.7rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.page-name {
  position: relative;
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
  width: fit-content;
  margin: 0 auto;
  padding-bottom: 1rem;
}

.page-name::after {
  content: "";
  position: absolute;
  display: block;
  height: 0.4rem;
  background: #0da6f3;
  bottom: 0;
  left: 0;
  width: 40%;
  border-radius: 2rem;
  animation: bouncingBorder 5s infinite alternate-reverse ease-in-out;
}

.page-description {
  text-align: center;
}

@keyframes bouncingBorder {
  to {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .app-container {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100dvb;
    grid-template-columns: 1.3fr 2fr;
  }
}
.app-container .image {
  position: relative;
  min-height: 20vh;
  max-height: 20vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 2px 2px #0da6f3 3rem -1.5rem;
}
@media (min-width: 768px) {
  .app-container .image {
    max-height: 100%;
  }
}
.app-container .image-1 {
  background-image: url(../img/1.jpg);
}
.app-container .image::after {
  position: absolute;
  content: "hairvana";
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 6rem;
  color: rgba(255, 255, 255, 0.4235294118);
  width: 100%;
  height: 100%;
  font-family: fantasy;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5176470588);
}
@media (min-width: 768px) {
  .app-container .image::after {
    font-size: 5rem;
  }
}
.app-container .app {
  width: 90%;
  margin: 0 auto;
  padding: 8rem 0;
}
.app-container .text-blue {
  color: #0da6f3;
}

.form {
  margin: 2rem auto;
  padding: 3rem 2rem;
  width: min(90%, 70rem);
}
.form .field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  column-gap: 2rem;
}
.form .field label {
  min-width: 10rem;
  font-weight: 700;
}
.form .field input[type=text],
.form .field input[type=password],
.form .field input[type=email],
.form .field input[type=text],
.form .field input[type=tel] {
  border: none;
  width: 60%;
  padding: 1.25rem;
  border-radius: 1rem;
}

.button.submit {
  display: block;
  width: 95%;
  padding: 1.5rem 1rem;
  outline: none;
  border: none;
  color: #FFFFFF;
  background: #0da6f3;
  border-radius: 1.5rem;
  text-align: center;
  font-weight: bolder;
  text-transform: uppercase;
  margin: 0 auto 1.5rem;
  margin-top: 4rem;
  max-width: 40rem;
}
.button.submit:hover, .button.submit:focus {
  cursor: pointer;
  opacity: 0.8;
}
@media (min-width: 768px) {
  .button.submit {
    max-width: 20rem;
    display: inline-block;
  }
}

.actions {
  display: flex;
  text-align: center;
  flex-direction: column;
  row-gap: 2rem;
}
@media (min-width: 1024px) {
  .actions {
    flex-direction: row;
    padding: 0 2rem;
    justify-content: space-between;
  }
}
.actions a {
  text-decoration: none;
  color: inherit;
  font-size: 2rem;
  text-decoration: underline;
}

.alerts-container {
  padding: 1rem 3rem;
}
.alerts-container .alert {
  display: block;
  border-radius: 1rem;
  padding: 1.5rem;
  max-width: 60rem;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #070707;
  margin: 0 auto 1.3rem;
  text-align: center;
  font-weight: 700;
  border: 3px solid #0da6f3;
  font-size: 1.5rem;
  transition: transform 300ms ease-in-out;
}
.alerts-container .alert.error {
  border-color: rgba(247, 56, 56, 0.7529411765);
}
.alerts-container .alert.success {
  border-color: rgba(125, 192, 94, 0.9176470588);
}
.alerts-container .alert:hover {
  transform: scale(1.05);
}